import env from '@/utils/evn.js';

function service(options = {}) {
	options.url = `${env.baseUrl}${options.url}`

	options.header = {
		'content-type': 'application/json',
	}
	
	// resolvedrejected
	return new Promise((resolved, rejected) => {
		uni.showLoading({
			title: "...",
			mask: true
		})
		options.success = (res) => {
			uni.hideLoading()
			// 0
			if (res.data.code !== 0) {
				// 
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: `${res.data.message}`
				})
				// 
				// 
				// rejected(res)
			} else {
				// 0
				uni.showToast({
					icon: 'none',
					duration: 1000,
					title: ''
				})
				resolved(res.data.data)
			}
		};
		options.fail = (err) => {
			// 
			uni.showToast({
				icon: 'none',
				duration: 3000,
				title: ','
			});
			// rejected(err)
		};
		uni.request(options)
	});
}

export default service;